import { delegate } from './../tools/utilities/event-tools';
import { getCurrentCart, ON_CART_FINISHED, ON_CART_PENDING, removeFromCart, updateCart } from '@process-creative/slate-theme-tools';
import { Cart } from '../tools/cart/cart';
import { debounce, getElem } from '../tools/utilities/helper-functions';
import { changeBundleQuantity } from './bundle/bundle-common';

const SELECTOR_REMOVE = '[data-cart-lines-remove]';

const SELECTOR_QUANTITY_MINUS = '[data-cart-quantity-minus]';
const SELECTOR_QUANTITY_PLUS = '[data-cart-quantity-plus]';
const SELECTOR_QUANTITY_NUMBER = '[data-cart-quantity-number]';
const SELECTOR_QUANTITY_CONTAINER = '[data-cart-quantity]';
const SELECTOR_CART_ITEM = '[data-cart-item]';

const SELECTOR_CART_CONTAINER = '[data-cart-container]';

const ATTR_CART_LINE = 'data-cart-line';

const CLASS_IS_HIDDEN = 'is-hidden';
const CLASS_HAS_INVALID_QUANTITY = 'has-invalid-quantity';

/**
 * Set the quantity of a line item.
 * @param line Line index within the cart.
 * @param quantity Cart quantity to set.
 * @returns Promise of the action happening, or NULL if no aciton.
 */
const quantitySet = async (line:number, quantity:number, container?: HTMLElement) => {
  if(isNaN(quantity) || !isFinite(quantity)) return;
  const cart = getCurrentCart() as Cart;
  const item = cart.items[line];
  

  if(item.properties?.isProductBundle){
    const bundleId = item.properties?.isProductBundle;
    const validationState = await changeBundleQuantity(bundleId, quantity);
    if(validationState == 'not enough inventory' || validationState =='all in cart'){
      const errorMessage = ` Quantity exceedes availability.`;
      setQuantityError(container as HTMLElement, errorMessage);
    } else {
      const cartContainer = getElem(SELECTOR_CART_CONTAINER)!;
      cartContainer.classList.remove(CLASS_HAS_INVALID_QUANTITY);
    }
  }else{

    if(quantity <= 0) {
      return await removeFromCart(line + 1);
    } else {
      return await updateCart(line + 1, quantity);
    }
  }
}

function setQuantityError(container: HTMLElement, errorMessage: string){
  const parentItem: HTMLElement = container.closest(SELECTOR_CART_ITEM)!;
  const errorElem = getElem('[data-error-elem]', parentItem)!;
  errorElem.classList.remove(CLASS_IS_HIDDEN);
  errorElem.innerHTML = errorMessage;
  const cartContainer = getElem(SELECTOR_CART_CONTAINER)!;
  cartContainer.classList.add(CLASS_HAS_INVALID_QUANTITY);
}

/**
 * Get the information about the quantity selector based off an event.
 * 
 * @param event Event to get from.
 * @returns The information about the quantity selector.
 */
const quantityGetSet = (event:HTMLElementEventMap['change'|'click']) => {
  const container: HTMLElement = (event.target as HTMLElement).closest(SELECTOR_QUANTITY_CONTAINER)!;
  const number = container?.querySelector<HTMLInputElement>(SELECTOR_QUANTITY_NUMBER)!;
  const line = parseInt(container.getAttribute(ATTR_CART_LINE)||'');
  return { container, number, line };
}

// Listen for Minus Click
delegate({
  element: document,
  event: 'click',
  selector: SELECTOR_QUANTITY_MINUS,
  callback: e => {
    const set = quantityGetSet(e);
    if(!set) return;
    const { container, number, line } = set;
    quantitySet(line, --number.valueAsNumber, container);
  }
});

// Listen for Plus Click
delegate({
  element: document,
  event: 'click',
  selector: SELECTOR_QUANTITY_PLUS,
  callback: e => {
    const set = quantityGetSet(e);
    if(!set) return;
    const { number, line, container } = set;
    quantitySet(line, ++number.valueAsNumber, container);
  }
});

// Listen for quantity change
delegate({
  element: document,
  events: ['input' ],
  selector: SELECTOR_QUANTITY_NUMBER,
  callback: e => {
    debounce(500, 'itemQty')
    .then(() => {      
      const set = quantityGetSet(e);
      if(!set) return;
      const { number, line, container } = set;
      quantitySet(line, number.valueAsNumber, container);
    })
    .catch(() => {})
  }
});

// Listen for remove click
delegate({
  element: document,
  event: 'click',
  selector: SELECTOR_REMOVE,
  callback: (e, target) => {
    e.preventDefault();
    const self = (target as HTMLButtonElement);
    const line = parseInt(self.getAttribute(ATTR_CART_LINE)||'');
    quantitySet(line, 0);
  }
});

// Listen for cart events
let isPending = false;
const pendingStateUpdate = () => {
  const conts = document.querySelectorAll<HTMLDivElement>(SELECTOR_QUANTITY_CONTAINER);

  // Disable input box
  conts.forEach(container => {
    const input = container.querySelector<HTMLInputElement>(SELECTOR_QUANTITY_NUMBER);
    if(input) input.disabled = isPending;
  })
}

$(document).on(ON_CART_PENDING, () => {
  isPending = true;
  pendingStateUpdate();
});

$(document).on(ON_CART_FINISHED, () => {
  isPending = false;
  pendingStateUpdate();
});
