import { getCurrentCart, ON_CART_FINISHED, printMoney } from '@process-creative/slate-theme-tools';
import { Cart } from '../tools/cart/cart';

const SELECTOR_CONTAINER = '[data-shipping-bar]';
const SELECTOR_DATA = '[data-shipping-bar-data]';
const SELECTOR_BAR = '[data-shipping-bar-bar]';
const SELECTOR_TITLE = '[data-shipping-bar-title]';

type ShippingProgressData = {
  shippingBarNotMet:string;
  shippingBarMet:string;
  shippingBarThreshold:number;
};

const updateShippingBar = (container:HTMLDivElement, data:ShippingProgressData) => {
  const cart = getCurrentCart() as Cart;
  
  const val = data.shippingBarThreshold * 100;
  const valRemaining = val - cart.total_price;
  const percComplete = Math.min(cart.total_price * 100 / val, 100);
  
  // Update Progress
  const elBar = container.querySelector<HTMLDivElement>(SELECTOR_BAR);
  if(elBar) elBar.style.width = `${percComplete.toFixed(2)}%`;

  // Update title
  const elTitle = container.querySelector<HTMLParagraphElement>(SELECTOR_TITLE);
  if(elTitle) {
    let title = data.shippingBarNotMet;
    if(valRemaining <= 0) title = data.shippingBarMet;
    title = title.replace('%amount%', printMoney(valRemaining, 'amount_no_decimals'));
    elTitle.textContent = title;
  }
}

let init = false;
document.addEventListener('readystatechange', () => {
  if(init) return;
  init = true;

  document.querySelectorAll<HTMLDivElement>(SELECTOR_CONTAINER).forEach(container => {
    const elementData = container.querySelector<HTMLScriptElement>(SELECTOR_DATA);
    if(!elementData) return;
    const data = JSON.parse(elementData.innerHTML) as ShippingProgressData;
    $(document).on(ON_CART_FINISHED, () => updateShippingBar(container, data));
  });
});