
import { updateCart} from '@process-creative/slate-theme-tools';
import { html, useState, component, unsafeHTML, useEffect } from '../../tools/utilities/component';

import { shopifyAjaxErrorGet } from '../../tools/utilities/error';
import { generateLoader } from '../../objects/loader';


component({
  name: 'quantity-selector',
  attributes: [ 'min', 'max', 'line', 'quantity', 'init', 'hiddenSelector' ],
  render: params => {
    if (!params.min || !params.max || !params.quantity || !params.line) return html`<span>Invalid Data</span>`;

    //States
    const min = parseInt(params.min);
    const max = parseInt(params.max);
    const lineindex = parseInt(params.line);
    const hidden = params.hiddenSelector || false;

    const [ pending, setPending ] = useState(false);

    
    const [ quantity, setQuantity ] = useState(parseInt(params.quantity||'0'));


    useEffect(() => {
      if(params.init !== 'true') {
        params.init = 'true';
        return;
      }

      const clamped = quantity < min ? min : quantity > max ? max : quantity;

      if(clamped != quantity) {
        setQuantity(clamped);
        return;
      }
      params.dispatchEvent(new CustomEvent('qtyUpdated', {
        detail: {quantity, params}
      }))
      
    }, [ quantity ])

    //Shorthands
    let minusDisabled = quantity <= min || pending;
    let plusDisabled = quantity >= max || pending ;
    let inputDisabled = max != null && min >= max;

    if (quantity >= max && max >= 0) setQuantity(max);

    //Disable inputs for hidden state selectors
    if(hidden) {
      minusDisabled = true;
      plusDisabled = true;
      inputDisabled = true;
    }
    //Events
    const addClick = (e: Event) => {
      setQuantity(quantity + 1);
    };
    const reduceClick = (e: Event) => {
      setQuantity(quantity - 1);
    };

    const eventFieldChange = (e:InputEvent) => {
      const element = e.currentTarget as HTMLInputElement;
      let value = element.value.replace(/\D/g,'');
      if(!value || !value.length) return;
      setQuantity(parseInt(value));
    };


    //Print
    return html`<div class="o-quantity-selector__inner has-js ${hidden?`is-hidden`:``}">
      <button type="button" 
        class="
          o-btn 
          o-quantity-selector__button 
          is-minus
          ${minusDisabled?'is-disabled':''}
        " 
        ?disabled=${minusDisabled}
        @click=${reduceClick}
      >
        ${unsafeHTML(`<svg viewBox="0 0 14 2" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0 0h14v2H0V0z" fill="#004851"/></svg>`)}
      </button>

      <div class="o-quantity-selector__input">
        <input 
          type="number" 
          class="o-input 
            o-quantity-selector__input-field
            ${inputDisabled?'is-disabled':''}" 
          .value=${quantity}
          @change=${eventFieldChange}
          data-quantity-selector-field
        >
      </div>
      <button type="button" 
        class="
          o-btn 
          o-quantity-selector__button 
          is-plus 
          ${plusDisabled?'is-disabled':''}
        " 
        ?disabled=${plusDisabled}
        @click=${addClick}
      >
        ${unsafeHTML(`<svg viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0 6h14v2H0V6z" fill="#004851"/><path d="M8 0v14H6V0h2z" fill="#004851"/></svg>`)}
      </button>
      ${pending?unsafeHTML(generateLoader({
        container: null, 
        clazz: null, 
        attributes: null,
        size: 16
      })):''}
    </div>`;
 }
});
