/**
 * Scripts used by the header component.
 * 
 */

//@ts-ignore
import $ from 'jquery';
import { getCurrentCart, ON_CART_FINISHED } from "@process-creative/slate-theme-tools";
import { Cart } from '../../tools/cart/cart';

const SELECTOR_CONTAINER = '[data-header]';
const SELECTOR_CART_COUNT = '[data-header-cart-counter]';

const CLASS_STICKY = 'is-sticky';
const CLASS_SCROLL = 'is-scroll-down';

document.querySelectorAll<HTMLElement>(SELECTOR_CONTAINER).forEach(headerContainer => {
  const topbarHeight = headerContainer.clientHeight;
  const setHeaderState = () => {
    let top = window.pageYOffset;

    // Visible state of the header
    if(top > topbarHeight) {
      headerContainer.classList.add(CLASS_STICKY);
    } else {
      headerContainer.classList.remove(CLASS_STICKY);
    }

    requestAnimationFrame(() => setHeaderState());
  }

  setHeaderState();

  let lastScrollTop = window.pageYOffset;
  const onHeaderScroll = () => {
    let top = window.pageYOffset;

    if (top > lastScrollTop) {
      headerContainer.classList.add(CLASS_SCROLL);
    } else {
      headerContainer.classList.remove(CLASS_SCROLL);
    }

    lastScrollTop = top <= 0 ? 0 : top;
  }

  document.addEventListener("scroll", onHeaderScroll);

  const onCartUpdate = () => {
    const countElement = document.querySelector<HTMLElement>(SELECTOR_CART_COUNT);
    if(!countElement) return;

    const cartItems = (getCurrentCart() as Cart).items;

    const count = cartItems.reduce((totalCount, item) => 
      item.properties?.isChildVariant ? totalCount : totalCount + item.quantity, 0
    )

    if (count > 0 && !countElement.classList.contains('has-items')) {
      countElement.classList.add('has-items');
    } else if (count <= 0 && countElement.classList.contains('has-items')) {
      countElement.classList.remove('has-items');
    }
    
    countElement.innerHTML = count ? count.toString() : '';
  }

  $(document).on(ON_CART_FINISHED, (e:Event) => onCartUpdate());
});
