import { getElem, getElems, onReady } from "../tools/utilities/helper-functions";

export const READ_MORE_CONTAINER = '[data-read-more-container]';
export const READ_MORE_TOGGLE = '[data-read-more-toggle]';

//Classes
export const CLASS_CONTENT_OPEN = 'is-open';

const container = getElem(READ_MORE_CONTAINER)!
const toggleButtons = getElems(READ_MORE_TOGGLE, container)!
toggleButtons.length && (() => {
  toggleButtons.forEach(button => {
    button.addEventListener('click', onReadMoreToggle);
  })

  function onReadMoreToggle(e: MouseEvent) {
    e.preventDefault(),e.stopPropagation();
    const isOpen = container.classList.contains(CLASS_CONTENT_OPEN)
    isOpen ?
      container.classList.remove(CLASS_CONTENT_OPEN) :
      container.classList.add(CLASS_CONTENT_OPEN)
  }
})()
