import { Product } from "../product/product";
import { Variant } from "../product/variant";


export function variantFetch(productHandle: string, variantId: number): Promise<Variant>{
  const fetchUrl = `/products/${productHandle}?view=json-variant&variant=${variantId}`;
  return fetch(fetchUrl).then(r => r.json())
}
export function productFetch(productHandle: string): Promise<Product>{
  const fetchUrl = `/products/${productHandle}?view=json-product`;
  return fetch(fetchUrl).then(r => r.json())
}