import { CLASS_FADE_IN, CLASS_FADE_OUT, CLASS_VISIBLE } from "../tools/constants/classes";
import { documentDisableScroll, documentEnableScroll } from "../tools/layout/document";
import { component, html, TemplateResult, unsafeHTML, useEffect, useState } from "../tools/utilities/component";
import { videoGetEmbed } from "../tools/video/url";
import * as Animation from './../settings/animation';

export const Modal = component({
  name: 'content-modal',
  attributes: [ 'visible', 'video', 'content' ],
  render: params => {
    //Determine default
    let defaultHtml:string|TemplateResult = '';
    let popupClass = 'o-modal__popup ';

    // Video
    if(params.video) {
      let iframeUrl = videoGetEmbed(params.video, {
        autoplay: true
      });

      if(iframeUrl) {
        popupClass += 'is-iframe'
        defaultHtml = html`
          <div class="o-modal__iframe">
            <iframe
              frameborder="0" allowfullscreen
              allow="autoplay; fullscreen" class="o-modal__iframe-frame"
              width="800" height="600"
              src="${iframeUrl}"
            >
            </iframe>
          </div>
        `;
      }
    }

    // Content
    if(params.content) {
      defaultHtml = html`
      <div class="o-modal__content">
        ${unsafeHTML(params.content)}
      </div>
      `;
    }

    if(!defaultHtml) defaultHtml = params.innerHTML;

    //States
    let visible = params.visible === "true";

    let fadeTimeout:NodeJS.Timeout;
    const [ fading, setFading ] = useState('');
    const [ content, setContent ] = useState(defaultHtml);

    useEffect(() => {
      if(fadeTimeout) clearTimeout(fadeTimeout);

      (async () => {
        if(visible) {
          //Wait 2 frames
          await new Promise(resolve => requestAnimationFrame(resolve));
          await new Promise(resolve => requestAnimationFrame(resolve));
          documentDisableScroll();
          setFading(CLASS_FADE_IN);
          return;
        }

        documentEnableScroll();
        setFading(CLASS_FADE_OUT);
        fadeTimeout = setTimeout(() => {
          setFading('');
        }, Animation.medium);
      })();
    }, [ visible ]);

    //Methods
    const modalClose = (e:MouseEvent) => {
      const el = e.target as HTMLDivElement;
      if(!el || !el.classList.contains('o-modal__frame-inner')) return;
      e.preventDefault();
      params.visible = "false";
    }

    document.addEventListener('keydown', e => {
      if(!visible || e.key !== 'Escape') return;
      e.preventDefault();
      params.visible = "false";
    });

    return html`
      <div class=${`o-modal ${visible?CLASS_VISIBLE:''} ${fading}`}>
        <div type="button" class="o-modal__backdrop"></div>
        <div class="o-modal__frame">
          <div class="o-modal__frame-inner" @click="${modalClose}">
            <div class=${popupClass}>
              ${ visible || fading ? (
                typeof content === 'string' ? unsafeHTML(content) : content
              ) : null }
            </div>
          </div>
        </div>
      </div>
    `;
  }
});