import { getElem, waitForNextFrame } from "../tools/utilities/helper-functions";
import {  cartAdd, getCurrentCart, ON_CART_FINISHED} from "@process-creative/slate-theme-tools"
import { Variant } from "../tools/product/variant";
import { addBundleToCart, validateVariantAvailability } from "./bundle/bundle-common";
import { LineItem } from "../tools/cart/line-item";
import { Product } from "../tools/product/product";
import { ValidationState } from "../types/types";
import { Cart } from "../tools/cart/cart";
import { productFetch, variantFetch } from "../tools/utilities/fetch-data";
import { CLASS_HIDDEN } from "../tools/constants/classes";


type CartUpsellProps = {
  template: 'cart-upsell-drawer' | 'cart-upsell-page'
}
export const createCartUpsell = ({template}: CartUpsellProps) => {
  

  const cartPageUpsellBlock = getElem('[data-cart-page-upsell-block]');

  let singleProduct: Product | null
  let cartItems: LineItem[]
  let upsellProduct: Product | null

  setupCartUpsell()
  $(document).on(ON_CART_FINISHED, () => setupCartUpsell());

  onUpsellAddClick(async () => {
    if (!(upsellProduct && upsellProduct.metafields && upsellProduct.metafields.doesnt_delete_previous_product && upsellProduct.metafields.doesnt_delete_previous_product === "true"))
      await removeExistingFromCart()
    await addUpsellToCart()
  })


  async function setupCartUpsell(){
    cartItems = (getCurrentCart() as Cart).items
    
    if(template == 'cart-upsell-page'){
      cartPageUpsellBlock!.classList.add(CLASS_HIDDEN)
    }

    const nonBundleProduct = await getSingleProduct()
    const bundleProduct = await  getSingleBundle()

    singleProduct = nonBundleProduct || bundleProduct
    
    if(singleProduct){ 
      upsellProduct = await getRelatedUpsellProduct()

      if(upsellProduct) {
        await addUpsellElemToDom();
        if(template == 'cart-upsell-page'){
          cartPageUpsellBlock!.classList.remove(CLASS_HIDDEN)
        }
        return
      }
    } else {
      if(template == 'cart-upsell-page'){
        cartPageUpsellBlock!.classList.add(CLASS_HIDDEN)
      }
    }

  }

  /* Implementation */

  async function addUpsellElemToDom(){
    const validationState = await validateUpsellAvailability()

    const upsellContainer = getElem('[data-cart-upsell-container]')!;
    if(!upsellContainer) return console.error('upsell container not found');

    if(validationState == 'ok'){
      const upsellElementString = await getUpsellElement()
      
      try{
        upsellContainer!.classList.add('is-visible')
        upsellContainer!.innerHTML = ''
        upsellContainer!.insertAdjacentHTML('afterbegin', upsellElementString!)
        waitForNextFrame() // bug where need frame refresh to fix
      } catch(e){
        console.error('e ' ,e);
      }
      
    } else {    
      upsellContainer!.classList.remove('is-visible')
      upsellContainer!.innerHTML = ''
    }

    return
  }

  async function getUpsellElement(): Promise<string | null>{

    return fetch(`/products/${upsellProduct!.handle}?view=${template}`,
    {headers: {'Content-Type': 'application/xhtml+xml'}})
    .then(response => response.text())
  }


  async function getSingleProduct(): Promise<Product | null>{
    const singleProduct = cartItems[0]
    if(cartItems.length != 1 || singleProduct.quantity > 1) return Promise.resolve(null)
    return productFetch(singleProduct.handle)
  }


  function getSingleBundle(): Promise<Product | null>{
    const {bundles, singleProds} = cartItems.reduce(({bundles, singleProds}, item) => {
      if(item.properties?.isProductBundle){
        bundles.push(item)
      } else if(!item.properties?.isChildVariant){
        singleProds.push(item)
      }
      return {bundles, singleProds}
    }, {bundles: [] as LineItem[], singleProds: [] as LineItem[]})

    if(singleProds.length || bundles.length != 1) return Promise.resolve(null)
  


    const bundle: LineItem =  bundles[0]
    if(bundle.quantity > 1) return Promise.resolve(null)

    return  productFetch(bundle.handle)
  }


  function getRelatedUpsellProduct(): Promise<Product | null>{
    const upsellHandle = singleProduct?.metafields.upsellHandle
    return upsellHandle ? productFetch(upsellHandle) : Promise.resolve(null)
  }


  function validateUpsellAvailability(): Promise<ValidationState>{
    
    const childVariants = upsellProduct!.metafields?.childVariants
    const isBundle = !!childVariants?.length

    if(isBundle){
      return validateBundleUpsell()
    } else {
      return Promise.resolve(validateSingleUpsell())
    }


    async function validateBundleUpsell(): Promise<ValidationState>{
      /* will validate based on extra items to add */
      const upsellBundleChildVariantObjs = upsellProduct!.metafields.childVariants
      if(!upsellBundleChildVariantObjs?.length) return Promise.resolve('error')
      const singleProductChildVariantObjs = singleProduct!.metafields?.childVariants
      const singleProductIsBundle = !!singleProductChildVariantObjs?.length


      if(singleProductIsBundle){  

        if(!singleProductChildVariantObjs.every(obj => upsellBundleChildVariantObjs.includes(obj))){
          console.error('Upsell bundle did not have all the children of single bundle')
          return 'error'
        }

      }else {
        const variantHandlesAndIds = upsellBundleChildVariantObjs!.map(varObj => {
          const varObjSplit = varObj.split(':')
          const productHandle = varObjSplit[0]
          const variantId = Number(varObjSplit[1])
          return {productHandle, variantId}
        })
        variantHandlesAndIds[0].variantId
        const existingProductInBundle = variantHandlesAndIds.some(({variantId}) => 
          singleProduct?.variants[0].id == variantId
        )

        if(!existingProductInBundle){
          console.error('Upsell bundle did not have all the children of the existing single product')
          return 'error'

        }

      }
        
      const variants: Variant[] = await Promise.all(
        upsellBundleChildVariantObjs!.map(varObj => {
          const varObjSplit = varObj.split(':')
          const productHandle = varObjSplit[0]
          const variantId = Number(varObjSplit[1])
          return variantFetch(productHandle, variantId)
        })
      )

      for(let variant of variants) {
        const validationState = validateVariantAvailability(variant)
        if(validationState != 'ok'){
          return validationState
        }
      }

      return 'ok'
    }

    function validateSingleUpsell(): ValidationState{
      return validateVariantAvailability(upsellProduct!.variants[0])
    }
  }


  async function removeExistingFromCart(){
    return fetch('/cart/clear')
  }

  async function addUpsellToCart(){
    const upsellBundleChildVariantObjs = upsellProduct!.metafields.childVariants
    const upsellIsBundle = !!upsellBundleChildVariantObjs?.length
    if(upsellIsBundle && !upsellProduct!.trackBundleChildInventoryDisabled){
      const childIdAndHandle = (
        upsellBundleChildVariantObjs!.map(varObj => {
          const varObjSplit = varObj.split(':')
          const productHandle = varObjSplit[0]
          const variantId = Number(varObjSplit[1])
          return {productHandle, variantId}
        })
      )

      const childVariants: Variant[] = await Promise.all(
        childIdAndHandle.map(({productHandle, variantId}) => {
          return variantFetch(productHandle, variantId)
        })
      )


      return addBundleToCart({
        bundle:{
          bundleProduct: upsellProduct!,
          childVariants,
          percentageDiscount: Number(upsellProduct!.metafields.percentageDiscount)
        }
      })
      
    } else {
      const bundleVariantId = upsellProduct!.variants[0].id
      await cartAdd({items: [{id: bundleVariantId, quantity: 1}]})

    }

  }




  function onUpsellAddClick(action: () => void){
    const SEL_UPSELL_ADD_TO_CART = '[data-upsell-add-to-cart]';
    document.addEventListener('click', e => {
      const targetIsButton = (e.target as Node).isSameNode(getElem(SEL_UPSELL_ADD_TO_CART))

      const targetIsInButton = (e.target as Element).closest(SEL_UPSELL_ADD_TO_CART)

      const clickTargetIsUpsellAddButton = targetIsButton || targetIsInButton
      
      
      if(clickTargetIsUpsellAddButton){
        e.preventDefault();
        e.stopPropagation();
        action()
      }
    })
  }
}

