import $ from 'jquery';

const contactForm = document.querySelector("form#contact_form");

const AddReCaptchaToken = (submitForm: any, requestName: string, callback: any) => {
  // @ts-ignore
  grecaptcha.execute(window.Shopify.recaptchaV3.siteKey, { action: requestName }).then(
    function (token: string) {
      let tokenInput = submitForm.querySelector("input[name=recaptcha-v3-token]");
      if (tokenInput instanceof HTMLElement)
        submitForm.setAttribute("value", token);
      else {
        tokenInput = document.createElement("input");
        tokenInput.setAttribute("name", "recaptcha-v3-token");
        tokenInput.setAttribute("type", "hidden");
        tokenInput.setAttribute("value", token);
        submitForm.appendChild(tokenInput);
      }
      if (callback)
        callback();
    }
  );
}

if (contactForm) {
  const formSubmitButtonSpan = contactForm.querySelector("button[type=\"submit\"] > span");
  const formSubmitButtonLoader = contactForm.querySelector("button[type=\"submit\"] > .c-footer__newsletter__submit-loader");
  contactForm.addEventListener("submit", (e) => {
    if (contactForm.hasAttribute("onsubmit"))
      contactForm.removeAttribute("onsubmit");
    e.preventDefault();
    if (formSubmitButtonSpan && !formSubmitButtonSpan.classList.contains("hidden"))
      formSubmitButtonSpan.classList.add("hidden");
    if (formSubmitButtonLoader && formSubmitButtonLoader.classList.contains("hidden"))
      formSubmitButtonLoader.classList.remove("hidden");
    const formType = contactForm.querySelector("input[name=\"form_type\"]");
    let formTypeValue = "customer";
    if (formType) {
      // @ts-ignore
      formTypeValue = formType.value;
    }
    AddReCaptchaToken(contactForm, formTypeValue, () => {
      let form: any = $(contactForm);
      let actionUrl = form.attr('action');
      $.ajax({
        type: "POST",
        async: true,
        url: actionUrl,
        data: $(form).serialize(),
        beforeSend: function(x) {
          x.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
        },
        success: function () {
          contactForm.querySelectorAll("input").forEach(object => {
            object.style.display = "none";
          });
          contactForm.querySelectorAll("button").forEach(object => {
            object.style.display = "none";
          });
          contactForm.querySelectorAll("p").forEach(object => {
            object.style.display = "flex";
          });
        },
        error: function () {
          // @ts-ignore
          contactForm.submit();
        }
      });
    });
  });
}
